* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: "Titillium Web", sans-serif;
}
h1 {
  text-align: center;
  letter-spacing: 3px;
  padding: 20px 0;
  background-color: #0f044c;
  color: white;
  border-bottom: 7px solid gold;
}

header {
  height: 100%;
  width: 100vw;
  /* padding-top: 100px; */
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: flex-start;
  padding: 40px;
  background: linear-gradient(to right, #c9d6ff, #e2e2e2);
}

.content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  width: 33%;
  margin-bottom: 100px;
  background-size: contain;
}

.content-data {
  height: 560px;
  width: 330px;
  background-color: white;
  border-radius: 3px;
  box-shadow: 6px 4px 12px rgba(0, 0, 0, 0.3), 0 0 40px rgba(0, 0, 0, 0.1) inset;
  transition: all 0.3s;
}

.content-data:hover {
  transform: scale(1.05, 1.05);
  transition: all 0.3s;
}

.poster {
  height: 65%;
  width: 100%;
}
.image {
  height: 100%;
  width: 100%;
}
.details {
  height: 25%;
  display: flex;
  flex-direction: column;
  /* align-items: center; */
  padding: 10px;
}
.movie_title {
  /* padding-left: 10px; */
  font-size: 23px;
  font-weight: 600;
  color: darkblue;
  text-align: left;
  cursor: default;
}
.info {
  font-size: 15px;
  color: rgb(167, 166, 166);
  cursor: default;
}

.watch_btn {
  height: 10%;
  display: flex;
  align-items: flex-end;
  justify-content: flex-end;
  margin: 0 10px 10px 0;
}
.watch_btn a {
  text-decoration: none;
  background-color: rgb(13, 196, 13);
  border-radius: 3px;
  color: white;
  height: 35px;
  width: 120px;
  font-size: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 10px;
}

@media only screen and (max-width: 1150px) {
  .content {
    width: 50%;
  }
}

@media only screen and (max-width: 784px) {
  .content {
    width: 100%;
  }
}
